<template>
  <div class="container">
    <page-title first-title="报名详情"></page-title>
    <!-- search -->
    <div class="enroll-unique-search-wrapper">
      <el-form :inline="true">
        <el-form-item label="用户ID：" :label-width="width">
          <el-input v-model="params.userID" placeholder="请输入用户ID" clearable></el-input>
        </el-form-item>
        <el-form-item label="用户手机号：" :label-width="width">
          <el-input v-model="params.userTel" placeholder="请输入用户手机号" clearable></el-input>
        </el-form-item>
        <br />
        <el-form-item label="报名时间：" :label-width="width">
          <el-date-picker
            v-model="params.startTime"
            type="datetime"
            placeholder="选择开始时间"
            value-format="timestamp"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="~" :label-width="width">
          <el-date-picker v-model="params.endTime" type="datetime" placeholder="选择结束时间" value-format="timestamp">
          </el-date-picker>
        </el-form-item>
        <br />
        <el-form-item label="保证金状态：" :label-width="width">
          <el-select v-model="params.status" clearable>
            <el-option v-for="item in status" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=" " :label-width="width">
          <el-button type="primary" @click="searchBid">搜索</el-button>
          <el-button type="primary" @click="clearParams">清空</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- table -->
    <div class="list-wrapper">
      <el-table
        :data="enrollData"
        style="width: 100%"
        border
        :header-cell-style="{ background: '#eef1f6' }"
        v-loading="loading"
      >
        <el-table-column label="用户ID" prop="uid"></el-table-column>
        <el-table-column label="用户手机号" prop="mask_phone"></el-table-column>
        <el-table-column label="保证金" prop="deposit"></el-table-column>
        <el-table-column label="报名时间" prop="created_at" :formatter="fmtTime"></el-table-column>
        <el-table-column label="保证金状态" prop="deposit_status" :formatter="fmtStatus"></el-table-column>
      </el-table>
    </div>
    <!-- pagination -->
    <div class="list-pagination-wrapper">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        background
        layout="prev, pager, next, jumper"
        :total="total"
        :hide-on-single-page="true"
      >
      </el-pagination>
    </div>
    <div style="text-align: center;">
      <el-button @click="$router.go(-1)">返回</el-button>
    </div>
  </div>
</template>

<script>
import { getEnrollList } from '../../api/bidding'
import { DEPOSIT_STATUS } from './static'
export default {
  name: 'EnrollRecord',
  data() {
    return {
      width: '120px',
      enrollData: [],
      id: this.$route.query.id ? this.$route.query.id : '',
      params: {
        userID: '',
        userTel: '',
        startTime: '',
        endTime: '',
        status: ''
      },
      status: DEPOSIT_STATUS,
      currentPage: 1,
      total: 0,
      loading: false
    }
  },
  created() {
    this.getList()
  },
  methods: {
    getList() {
      this.loading = true
      getEnrollList(this.id, this.params, this.currentPage)
        .then(
          res => {
            this.total = res.paged.total
            this.enrollData = res.list
          },
          err => {
            this.$message.error(err.errorMsg)
          }
        )
        .finally(() => {
          this.loading = false
        })
    },
    searchBid() {
      this.getList()
    },
    clearParams() {
      for (let key in this.params) {
        this.params[key] = ''
      }
    },
    handleCurrentChange() {
      this.getList()
    },
    fmtTime(row, column, cellValue) {
      return this.utils.formatDate('YYYY-MM-DD HH:mm:ss', cellValue * 1e3)
    },
    fmtStatus(row, column, cellValue) {
      let s
      this.status.forEach(item => {
        if (item.id == cellValue) {
          s = item.name
        }
      })
      return s
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  & > div {
    margin-top: 20px;
  }
  .list-pagination-wrapper {
    text-align: center;
  }
}
</style>

<style lang="scss">
.enroll-unique-search-wrapper {
  .el-form-item__label {
    text-align: center;
  }
}
</style>
